































import Vue from 'vue'
import RecipeApi, { Recipe } from '../services/RecipeApi'
import VueLazyload from 'vue-lazyload'
import he from 'he'

Vue.use(VueLazyload)

export default Vue.extend({
  name: 'Result' as string,

  data: function () {
    return {
      result: this.data
    }
  },

  methods: {
    unescape: function (tounescape) {
      return he.decode(tounescape)
    },

    openLink: function (link) {
      window.open(link)
    }
  },

  props: {
    data: {
      default: null,
      type: Array,
      require: true
    },
    type: {
      default: 'recipe',
      type: String,
      require: true
    },

    language: {
      default: '',
      type: String,
      required: false
    },

    domain: {
      default: '',
      type: String,
      required: false
    }
  }
})
